import axios from 'axios'
import helper from './helper'
// let qs = require('querystring')
import qs from 'qs'

axios.defaults.withCredentials = true
const baseURL = process.env.API_ROOT
//console.log( process.env.NODE_ENV)  
//判断环境提供baseURL，注意要与后台地址一致
// let root = 'https://wxpay.zhouyujunlin.com:8082'
let root =baseURL
//  let root = 'http://127.0.0.1:80'
    // process.env.NODE_ENV === 'development'
    // let root = 'http://wxpay.zhouyujunlin.com:8202'
    //   // 开发环境api接口  http://192.168.0.105:80    y
    //   ? http://192.168.0.199:80
    //  测试环境 `http://wxpay.zhouyujunlin.com:8202/`
    //   // 生产环境api接口  https://wxpay.zhouyujunlin.com:8082
    //   :
    //   `http://wxpay.zhouyujunlin.com:8082`;
    // 引用axios，设置头文件
axios.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded';
axios.defaults.headers.common['token'] = "eyJ0eXAiOiJKV1QiLCJhbGciOiJIUzI1NiJ9.eyJpYXQiOjE2NDQ4MDg3OTcsImV4cCI6OTQyMDgwODc5NywiZGF0YSI6eyJ1c2VyX2lkIjo2NTJ9fQ.LuB8FdVWA_2sZQjdxjsFHfg5lnGn5SfHL1WqTrC8TUE"

function apiAxios(method, url, data, token) {
    return axios({
        method: method,
        //拼接参数
        url: method === 'GET' || method === 'DELETE' ? helper.queryString(url, data) : url,
        data: method === 'POST' || method === 'PUT' ? data : null,
        baseURL: root,
        timeout: 10000,
        // headers: { Authorization: `Bearer ${token}` },
        headers: {'Cache-Control': 'no-cache'},
        withCredentials: false
    })
}


export default {
    get: function(url, params, token) {
        return apiAxios('GET', url, params, token)
    },
    post: function(url, params, token) {
        return apiAxios('POST', url, params, token)
    },
    put: function(url, params, token) {
        return apiAxios('PUT', url, params, token)
    },
    delete: function(url, params, token) {
        return apiAxios('DELETE', url, params, token)
    },
}


