import http from './http.js'
export default {
    //美图秀秀
    //clickid 拿到uuid 和ClickId//价格数据
    // clickId(clickId,mockUserId,data) {
    //     return http.get("/v1/click/api/" + clickId + "/mockUserId/" + mockUserId, data)
    // },
    upImage(data){
        return http.post("/v1/base/upload_file_to_qiniuyun",data)
    },
    upPic(data){
        return http.post("/shanghu/Jieshao/aiApiYingyezhizhao",data)
    },
    postSort(data){
        return http.post("/shanghu/base/canyin_types",data)
    },
    postPlace(data){
        return http.post("/shanghu/Place/place_diqu",data)
    },
    postGov(data){
        return http.post("/shanghu/Place/place_jianguansuo",data)
    },
    postStreet(data){
        return http.post("/shanghu/Place/place_shichang",data)
    },
    postMarket(data){
        return http.post("/shanghu/login/get_shichang",data)
    },
    upDataForm(data){
        return http.post("/shanghu/login/update_shanghu_info",data)
    }

}


