import Vue from 'vue'
import Router from 'vue-router'
import homepage from '@/components/homepage'
import upImg from '@/components/upImg'




Vue.use(Router)

export default new Router({
    // base: "zhongtong",   //记得打版本号
    // mode: 'history',  //去掉url中的#
    routes: [     
        {
            // path: '/danye/index/index1.pythasp',
            path: '/',
            name: 'homepage',
            component: homepage,
        },
        {
            path: '/upImg',
            name: 'upImg',
            component: upImg,
        },
    ]
})


